.list-no-style {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto;
}

.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
  padding-top: 20px;

  @media screen and (min-width: $tablet) {
    width: 768px;
    padding: 0 75px;
    padding-top: 60px;
  }

  @media screen and (min-width: $desktop) {
    width: 1024px;
    padding: 0 71px;
    padding-top: 60px;
  }
}
